.App {
  text-align: center;
  margin: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.container-table{
  display: flex;
  justify-content: center;
}

table, td, th {  
  border: 1px solid #ddd;
  text-align: center;
}
th{
  background: #d1d1d1;
}

table {
  border-collapse: collapse;
  width: 100%;
  max-width: 800px;
}

th, td {
  padding: 10px;
}


input{
  padding-right: 0;
}
textarea{
  width: 100%;
}

button{
  border: none;
  padding: 10px 20px;
  color: white;
  font-weight: bold;
  margin: 20px 5px;
}

button:hover{
  opacity: 0.7;
  cursor: pointer;
}

.red{
  background: red;
}
.blue{
  background: blue;
}
.green{
  background: green;
}

.input-name{
  width: 250px;
  margin-left: 10px;
  padding: 3px;
}


/* spinner */
.loader {
  border: 5px solid #f3f3f3;
  border-radius: 50%;
  border-top: 5px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 0.5s linear infinite; /* Safari */
  animation: spin 0.5s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}